    .fire-button {
      display: flex;
      align-items: center;
      padding: 11px 38px;
      overflow: hidden;

      span {
        color: $white;
        margin-left: 7px;
        font-size: 16px;
        font-weight: 500;

        [dir="rtl"] & {
          margin-left: unset;
          margin-right: 7px;
        }
      }

      .fire {
        position: relative;
        @include pseudowh($width: 20px, $height: 20px);
        background-color: transparent;
        z-index: 1;

        img {
          margin-top: -11px;
        }

        .fire-main {
          @include pseudowh;
          position: absolute;
          animation: scaleUpDown 3s ease-out;
          animation-iteration-count: infinite;
          animation-fill-mode: both;

          .main-fire {
            @include pseudowh;
            position: absolute;
            background-image: radial-gradient(farthest-corner at 10px 0,
                $white 0%,
                #ddd 95%);
            transform: scaleX(0.8) rotate(45deg);
            border-radius: 0 40% 60% 40%;
          }

          .particle-fire {
            @include pseudowh($width: 10px, $height: 10px);
            position: absolute;
            top: 60%;
            left: 45%;
            background-color: $white;
            border-radius: 50%;
            animation: particleUp 2s ease-out 0;
            animation-iteration-count: infinite;
            animation-fill-mode: both;
          }
        }

        .fire-right {
          @include pseudowh;
          position: absolute;
          animation: shake 2s ease-out 0;
          animation-iteration-count: infinite;
          animation-fill-mode: both;

          .main-fire {
            @include pseudowh($width: 80%, $height: 80%);
            position: absolute;
            top: 15%;
            right: -25%;
            background-color: $white;
            transform: scaleX(0.8) rotate(45deg);
            border-radius: 0 40% 60% 40%;

            .particle-fire {
              @include pseudowh($width: 15px, $height: 15px);
              position: absolute;
              top: 45%;
              left: 50%;
              background-color: $white;
              transform: scaleX(0.8) rotate(45deg);
              border-radius: 50%;
              animation: particleUp 2s ease-out 0;
              animation-iteration-count: infinite;
              animation-fill-mode: both;
            }
          }
        }

        .fire-left {
          @include pseudowh;
          position: absolute;
          animation: shake 3s ease-out 0;
          animation-iteration-count: infinite;
          animation-fill-mode: both;

          .main-fire {
            @include pseudowh($width: 80%, $height: 80%);
            position: absolute;
            top: 15%;
            left: -20%;
            background-color: $white;
            transform: scaleX(0.8) rotate(45deg);
            border-radius: 0 40% 60% 40%;

            .particle-fire {
              @include pseudowh($width: 10%, $height: 10%);
              position: absolute;
              top: 10%;
              left: 20%;
              background-color: $white;
              border-radius: 50%;
              animation: particleUp 3s infinite ease-out 0;
              animation-fill-mode: both;
            }
          }
        }
      }
    }
