                letter-spacing: 0.3px;

                &::before {
                  width: 65px;
                }
              }
            }

            img {
              width: 22px;
              height: 22px;
              object-fit: contain;
            }

            h6 {
              font-size: 17px;
              font-weight: 500;
              position: relative;
              margin-left: 10px;
