      }

      .location-box-2 {
        display: flex;
        align-items: center;
        margin-right: 20px;
        position: relative;

        [dir="rtl"] & {
          margin-right: unset;
          margin-left: 20px;
        }

        @include mq-max(xl) {
          margin-right: unset;
          margin-left: 0;
        }

        button {
          display: flex;
          align-items: center;
          padding: 0;

          &:focus {
            box-shadow: none;
          }

          .icli {
            margin-right: 9px;
            font-size: 22px;
            color: $white;

            [dir="rtl"] & {
              margin-right: unset;
              margin-left: 9px;
            }

            @include mq-max(lg) {
              @include flex_common;
              @include pseudowh($width: calc(35px + (46 - 35) * ((100vw - 320px) / (1920 - 320))),
                $height: calc(35px + (46 - 35) * ((100vw - 320px) / (1920 - 320))));
              margin-right: 0;
            }
          }

          span {
            font-size: 16px;
            color: $white;

            @include mq-max(lg) {
              display: none;
            }
          }

          .down-arrow {
            margin-left: 10px;
            color: $white;

            [dir="rtl"] & {
              margin-left: unset;
              margin-right: 10px;
            }

            @include mq-max(lg) {
              display: none;
            }
          }
        }
      }
    }
  }

  .rightside-menu {
    display: flex;
    align-items: center;
    margin-left: auto;

    &.support-sidemenu {
      @media (max-width: 1432px) {
        display: none;
      }
    }

    [dir="rtl"] & {
      margin-left: unset;
      margin-right: auto;
    }

    @include mq-max(xl) {
      position: absolute;
      top: -49px;
      right: 0;

      [dir="rtl"] & {
        right: unset;
        left: 0;
      }
    }

    @include mq-max(lg) {
      top: -53px;
      right: 78px;
    }

    @include mq-max(md) {
      top: -51px;
    }

    @include mq-max(xs) {
      top: -49px;
      right: 58px;
    }

    @include mq-max(2xs) {
      right: 64px;
    }

    .dropdown-dollar {
      display: flex;
      align-items: center;
      margin-right: 22px;

      @include mq-max(lg) {
        display: none;
      }

      .dropdown {
        position: relative;
        z-index: 0;

        .dropdown-toggle {
          font-size: 16px;
          display: flex;
          align-items: center;
          font-weight: 500;
          position: relative;
          border: none;
          background-color: transparent;
          padding: 0;

          i {
            margin-left: 8px;

            [dir="rtl"] & {
              margin-left: unset;
              margin-right: 8px;
            }
          }

          &::after {
            display: none;
          }
        }

        +.dropdown {
          margin-left: 23px;

          [dir="rtl"] & {
            margin-left: unset;
            margin-right: 23px;
          }

          &::after {
            @include pos;
            @include center(vertical);
            @include pseudowh($width: 1px, $height: 25px);
            left: -10px;
            background-color: $border-color;

            [dir="rtl"] & {
              left: unset;
              right: -10px;
            }
          }
        }

        .dropdown-menu {
          min-width: 100%;

          li {
            display: block;
          }
        }
      }
    }

    .wishlist-box {
      display: flex;
      align-items: center;

      .header-icon {
        @include pseudowh($width: calc(35px + (46 - 35) * ((100vw - 320px) / (1920 - 320))),
          $height: calc(35px + (46 - 35) * ((100vw - 320px) / (1920 - 320))));
        @include flex_common;
        font-size: 18px;
        border: 1px solid $border-color;
        background-color: $white;
        border-radius: 100%;
        padding: 10px;
        position: relative;

        +.header-icon {
          margin-left: 38px;

          &::after {
            @include pos;
            @include center(vertical);
            @include pseudowh($width: 1px, $height: 100%);
            left: -50%;
            background-color: $border-color;
          }
        }

        &.swap-icon {
          margin-left: 0;

          &::after {
            content: none;
          }
        }

        &.bag-icon {
          display: block;

          @include mq-max(xs) {
            display: none;
          }
        }

        &.search-icon,
        &.user-icon {
          display: none;

          @include mq-max(xl) {
            display: flex;

            +.header-icon {
              margin-left: 38px;

              &::after {
                @include pos;
                @include center(vertical);
                @include pseudowh($width: 1px, $height: 100%);
                left: -50%;
                background-color: $border-color;
              }
            }
          }
        }

        .badge-number {
          @include pseudowh($width: 18px, $height: 18px);
          @include flex_common;
          position: absolute;
          top: -10px;
          right: 0;
          background: var(--theme-color2);
          font-weight: 600;
          color: $white;
          border-radius: 100%;
          font-size: 10px;
        }

        .icli {
          color: $black;
          font-size: calc(18px + (22 - 18) * ((100vw - 320px) / (1920 - 320)));

          &.iconly-Swap {
            transform: rotate(90deg);
          }
        }
      }
    }

    .option-list-2 {
      display: flex;

      [dir="rtl"] & {
        padding-right: 0;
      }

      li {
        position: relative;

        &:nth-of-type(5n-4) {
          display: none;

          +li {
            margin-left: 0;

            [dir="rtl"] & {
              margin-left: unset;
              margin-right: 0;
            }

            &::after {
              content: none;
            }
          }
        }

        @include mq-max(xl) {
          &:nth-of-type(5n-4) {
            display: block;

            +li {
              margin-left: 22px;

              [dir="rtl"] & {
                margin-left: unset;
                margin-right: 22px;
              }

              &::after {
                content: "";
              }
            }
          }
        }

        @include mq-max(md) {
          &:not(:nth-last-of-type(5n-5)) {
            display: none;
          }
        }

        .header-icon {
          @include pseudowh($width: calc(35px + (46 - 35) * ((100vw - 320px) / (1920 - 320))),
            $height: calc(35px + (46 - 35) * ((100vw - 320px) / (1920 - 320))));
          @include flex_common;
          font-size: 18px;
          border: 1px solid $border-color;
          background-color: $white;
          border-radius: 100%;
          padding: 10px;
          position: relative;

          @include mq-max(xl) {
            border: none;
            background-color: transparent;
          }

          +.header-icon {
            margin-left: 38px;

            &::after {
              @include pos;
              @include center(vertical);
              @include pseudowh($width: 1px, $height: 100%);
              left: -50%;
              background-color: $border-color;
            }
          }

          .badge-number {
            @include pseudowh($width: 18px, $height: 18px);
            @include flex_common;
            position: absolute;
            top: -10px;
            right: 0;
            background-color: var(--theme-color);
            font-weight: 600;
            color: $white;
            border-radius: 100%;
            font-size: 10px;
          }

          .badge-light {
            @include mq-max(xl) {
              background-color: $white;
              color: var(--theme-color);
              top: -3px;
            }
          }

          .icli {
            color: $black;
            font-size: calc(18px + (22 - 18) * ((100vw - 320px) / (1920 - 320)));

            &.iconly-Swap {
              transform: rotate(90deg);
            }

            @include mq-max(xl) {
              color: $white;
            }
          }
        }

        +li {
          margin-left: 37px;

          [dir="rtl"] & {
            margin-left: unset;
            margin-right: 37px;
          }

          @include mq-max(4xl) {
            margin-left: 22px;

            [dir="rtl"] & {
              margin-left: unset;
              margin-right: 22px;
            }
          }

          &::after {
            @include pos;
            @include center(vertical);
            @include pseudowh($width: 1px, $height: 80%);
            left: -20px;
            background-color: $border-color;

            [dir="rtl"] & {
              left: unset;
              right: -20px;
            }

            @include mq-max(4xl) {
              left: -13px;

              [dir="rtl"] & {
                left: unset;
                right: -13px;
              }
            }

            @include mq-max(xl) {
              background-color: rgba($white, 0.2);
            }
          }
        }
      }
    }

    .user-box {
      @include flex_wrap($dis: flex, $wrap: nowrap, $gap: 12px);
      align-items: center;
      margin-left: 37px;
      position: relative;

      [dir="rtl"] & {
        margin-left: unset;
        margin-right: 37px;
      }

      @include mq-max(4xl) {
        margin-left: 22px;

        [dir="rtl"] & {
          margin-left: unset;
          margin-right: 22px;
        }
      }

      @include mq-max(md) {
        margin-left: 0;

        [dir="rtl"] & {
          margin-left: unset;
          margin-right: 0;
        }
      }

      &::after {
        @include pos;
        @include center(vertical);
        @include pseudowh($width: 1px, $height: 80%);
        left: -20px;
        background-color: $border-color;

        [dir="rtl"] & {
          left: unset;
          right: -20px;
        }

        @include mq-max(4xl) {
          left: -13px;

          [dir="rtl"] & {
            left: unset;
            right: -13px;
          }
        }

        @include mq-max(xl) {
          background-color: rgba($white, 0.2);
        }

        @include mq-max(md) {
          content: none;
        }
      }

      .header-icon {
        @include pseudowh($width: calc(35px + (46 - 35) * ((100vw - 320px) / (1920 - 320))),
          $height: calc(35px + (46 - 35) * ((100vw - 320px) / (1920 - 320))));
        @include flex_common;
        font-size: 18px;
        border: 1px solid $border-color;
        background-color: $white;
        border-radius: 100%;
        padding: 10px;
        position: relative;

        @include mq-max(xl) {
          border: none;
          background-color: transparent;
        }

        .badge-number {
          @include pseudowh($width: 18px, $height: 18px);
          @include flex_common;
          position: absolute;
          top: -10px;
          right: 0;
          background-color: var(--theme-color);
          font-weight: 600;
          color: $white;
          border-radius: 100%;
          font-size: 10px;
        }

        .icli {
          color: $black;
          font-size: calc(18px + (22 - 18) * ((100vw - 320px) / (1920 - 320)));

          @include mq-max(xl) {
            color: $white;
          }
        }
      }

      &:hover {
        .user-name {
          h4 {
            color: var(--theme-color);
          }
        }
      }

      .user-name {
        @media (max-width: 1660px) {
          display: none;
        }
      }
    }

    .mobile-app {
      position: relative;
      border-radius: 5px;
      padding: 16px 18px;
      display: flex;
      overflow: hidden;
      align-items: center;
      margin-left: 18px;
      color: var(--theme-color);

      &::before {
        @include pos;
        @include pseudowh;
        top: 0;
        left: 0;
        background-color: var(--theme-color);
        opacity: 0.1;
        z-index: -1;
      }

      [dir="rtl"] & {
        margin-left: unset;
        margin-right: 18px;
      }

      .mobile-image {
        margin-right: 11px;

        [dir="rtl"] & {
          margin-right: unset;
          margin-left: 11px;
        }

        img {
          @include pseudowh($width: 15px, $height: auto);
        }
      }
    }

    .dropdown-user {
      display: flex;
      align-items: center;

      .user-detail {
        margin-left: 15px;

        @include mq-max(2xl) {
          display: none;
        }

        h6 {
          color: $content-color;
          font-size: 14px;
          font-weight: 400;
          margin-bottom: 2px;
        }

        h5 {
          color: $title-color;
          font-size: 16px;
          margin-bottom: 0;
        }
      }
    }
  }

  .right-nav {
    float: right;
    display: flex;
    align-items: center;
    margin-left: auto;

    [dir="rtl"] & {
      margin-left: unset;
      margin-right: auto;
    }

    @include mq-max(2xl) {
      display: none;
    }

    .nav-number {
      display: flex;
      align-items: center;

      @include mq-max(4xl) {
        display: none;
      }

      img {
        @include pseudowh($width: 32px, $height: 32px);
      }

      span {
        font-size: calc(18px + (27 - 18) * ((100vw - 320px) / (1920 - 320)));
        font-weight: 500;
        margin-bottom: 0;
        margin-left: 8px;

        [dir="rtl"] & {
          margin-left: unset;
          margin-right: 8px;
        }
      }
    }

    .fire-button {
      display: flex;
      align-items: center;
      padding: 11px 38px;
      overflow: hidden;

      span {
        color: $white;
        margin-left: 7px;
        font-size: 16px;
        font-weight: 500;

        [dir="rtl"] & {
          margin-left: unset;
          margin-right: 7px;
        }
      }

      .fire {
        @include pseudowh($width: 20px, $height: 20px);
        position: relative;
        background-color: transparent;
        z-index: 1;

        .fire-main {
          @include pseudowh;
          position: absolute;
          animation: scaleUpDown 3s ease-out;
          animation-iteration-count: infinite;
          animation-fill-mode: both;

          .main-fire {
            @include pseudowh;
            position: absolute;
            background-image: radial-gradient(farthest-corner at 10px 0,
                $white 0%,
                #ddd 95%);
            transform: scaleX(0.8) rotate(45deg);
            border-radius: 0 40% 60% 40%;
          }

          .particle-fire {
            @include pseudowh($width: 10px, $height: 10px);
            position: absolute;
            top: 60%;
            left: 45%;
            background-color: $white;
            border-radius: 50%;
            animation: particleUp 2s ease-out 0;
            animation-iteration-count: infinite;
            animation-fill-mode: both;
          }
        }

        .fire-right {
          @include pseudowh;
          position: absolute;
          animation: shake 2s ease-out 0;
          animation-iteration-count: infinite;
          animation-fill-mode: both;

          .main-fire {
            @include pseudowh($width: 80%, $height: 80%);
            position: absolute;
            top: 15%;
            right: -25%;
            background-color: $white;
            transform: scaleX(0.8) rotate(45deg);
            border-radius: 0 40% 60% 40%;

            .particle-fire {
              @include pseudowh($width: 15px, $height: 15px);
              position: absolute;
              top: 45%;
              left: 50%;
              background-color: $white;
              transform: scaleX(0.8) rotate(45deg);
              border-radius: 50%;
              animation: particleUp 2s ease-out 0;
              animation-iteration-count: infinite;
              animation-fill-mode: both;
            }
          }
        }

        .fire-left {
          @include pseudowh;
          position: absolute;
          animation: shake 3s ease-out 0;
          animation-iteration-count: infinite;
          animation-fill-mode: both;

          .main-fire {
            @include pseudowh($width: 80%, $height: 80%);
            position: absolute;
            top: 15%;
            left: -20%;
            background-color: $white;
            transform: scaleX(0.8) rotate(45deg);
            border-radius: 0 40% 60% 40%;

            .particle-fire {
              @include pseudowh($width: 10%, $height: 10%);
              position: absolute;
              top: 10%;
              left: 20%;
              background-color: $white;
              border-radius: 50%;
              animation: particleUp 3s infinite ease-out 0;
              animation-fill-mode: both;
            }
          }
        }
      }
    }
  }

  .dropdown-category {
    @include flex_common;
    border: none;
    background-color: transparent;
    padding: 9px calc(16px + (31 - 16) * ((100vw - 320px) / (1920 - 320))) 9px 0;
    position: relative;

    [dir="rtl"] & {
      padding: 9px 0 9px calc(16px + (31 - 16) * ((100vw - 320px) / (1920 - 320)));
    }

    @include mq-max(xl) {
      display: none;
    }

    &::after {
      @include pos;
      @include pseudowh($width: 1px, $height: 100%);
      top: 0;
      right: 0;
      background-color: $border-color;

      [dir="rtl"] & {
        right: unset;
        left: 0;
      }
    }

    .icli {
      font-size: 23px;
      color: var(--theme-color);
      margin-right: 10px;

      [dir="rtl"] & {
        margin-right: unset;
        margin-left: 10px;
      }
    }

    span {
      font-size: 16px;
      margin-top: 4px;
    }
  }

  .dropdown-list {
    @include flex_common;
    font-family: $public-sans;

    @include mq-max(md) {
      display: none;
    }

    li {
      &:last-child {
        margin-right: 0;
      }

      +li {
        .dropdown {
          margin-left: 20px;

          &::after {
            @include pos;
            @include center(vertical);
            @include pseudowh($width: 1px, $height: 80%);
            left: -12px;
            background-color: rgba($white, 0.4);
          }
        }
      }

      &:first-child {
        &::after {
          content: none;
        }
      }

      .dropdown {
        .dropdown-toggle {
          width: auto;
          display: block;
          background-color: transparent;
          border-radius: 5px;
          font-weight: 500;
          font-size: unset;

          &:after {
            content: none;
          }

          span {
            font-weight: 400;
          }

          .fa-chevron-down {
            margin-left: 10px;
          }
        }

        .dropdown-menu {
          .dropdown-list {
            display: block;

            +.dropdown-list {
              margin-top: 10px;
              text-overflow: ellipsis;
              white-space: nowrap;
            }
          }
        }
      }
    }
  }

  .about-list {
    font-family: $public-sans;

    @include mq-max(lg) {
      display: none;
    }

    li {
      position: relative;
      margin-left: 15px;
      font-size: 12px;
      font-weight: 500;

      &:first-child {
        margin-left: 0;

        &::after {
          content: none;
        }
      }

      &::after {
        @include pos;
        @include center(vertical);
        @include pseudowh($width: 1px, $height: 70%);
        left: -8px;
        background-color: rgba($white, $alpha: 0.4);
      }
    }
  }
}

header {
  &.header-gradient {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    z-index: 1;

    .navbar-light {
      .navbar-nav {
        .nav-link {
          color: $white;
        }
      }
    }

    .header-top {
      background: linear-gradient(to right,
          var(--theme-color),
          var(--theme-color2));
    }

    .dropdown {
      &::before {
        color: $white;
      }
    }

    .top-nav {
      .navbar-top {
        .rightside-box {
          .right-side-menu {
            .right-side {
              .header-wishlist {
                .feather {
                  color: $white;
                }
              }

              .delivery-login-box {
                .delivery-icon {
                  .feather {
                    color: $white;
                  }
                }

                .delivery-detail {

                  h6,
                  h5 {
                    color: rgba($white, 0.8);
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}