/**=====================
     Product scss
==========================**/
.add-to-cart-box {
  background-color: $light-gray;
  border-radius: 50px;
  position: relative;
  max-width: 250px;

  .btn-add-cart {
    padding: calc(8px + (10 - 8) * ((100vw - 320px) / (1920 - 320))) 0;
    width: 100%;
    font-size: calc(14px + (15 - 14) * ((100vw - 320px) / (1920 - 320)));
    margin-top: 10px;
    color: $content-color;
    border-radius: 50px;
    font-weight: 500;

    .add-icon {
      @include center(vertical);
      @include flex_common;
      @include pseudowh($width: calc(30px + (37 - 30) * ((100vw - 320px) / (1920 - 320))),
        $height: calc(30px + (37 - 30) * ((100vw - 320px) / (1920 - 320))));
      background-color: $white;
      border-radius: 100%;
      color: var(--theme-color);
      position: absolute;
      right: 4px;

      &.bg-light-orange {
        background-color: #f9f8f6;
      }

      &.bg-light-gray {
        background-color: $light-gray;
      }

      @include mq-max(xs) {
        display: none;
      }

      i {
        font-size: 13px;
      }
    }
  }

  .qty-box {
    @include pseudowh;
    position: absolute;
    top: 0;
    left: 0;
    margin: 0;
    display: none;

    &.open {
      display: block;
    }

    .input-group {
      background-color: $light-gray;
      border-radius: 50px;
      padding: 4px;
      text-align: center;
      z-index: 0;

      &.theme-bg-white {
        background-color: $white;
      }

      button {
        @include pseudowh($width: calc(29px + (35 - 29) * ((100vw - 320px) / (1920 - 320))),
          $height: calc(29px + (35 - 29) * ((100vw - 320px) / (1920 - 320))));
        @include flex_common;
        border: none;
        padding: 0;
        background-color: $white;
        z-index: 0;
        border-radius: 100% !important;

        i {
          font-size: 13px;
          color: var(--theme-color);
          margin-top: 1px;
        }

        &:focus {
          box-shadow: none;
        }
      }

      input {
        height: auto;
        background-color: transparent;
        border: none;
        padding: 0;
        text-align: center;
        font-size: 14px;
        color: $content-color;
      }
    }
  }
}

.add-to-cart-btn-2 {
  .btn {
    @include flex_common;
    @include pseudowh($width: calc(30px + (37 - 30) * ((100vw - 320px) / (1920 - 320))),
      $height: calc(30px + (37 - 30) * ((100vw - 320px) / (1920 - 320))));
    position: absolute;
    right: 0;
    bottom: 0;
    background-color: #f7f7f7;
    padding: 0;
    border-radius: 100%;
    color: var(--theme-color);

    [dir="rtl"] & {
      right: unset;
      left: 0;
    }
  }

  .qty-box-2 {
    display: none;
    bottom: 0;
    position: absolute;
    left: 0;
    width: 100%;
    height: auto;
    margin-left: auto;
    max-width: 190px;

    [dir="rtl"] & {
      left: unset;
      right: 0;
    }

    &.qty-box-3 {
      max-width: 203px;
    }

    &.open {
      display: block;
    }

    .input-group {
      background-color: $light-gray;
      border-radius: 5px;
      padding: 4px;
      text-align: center;
      z-index: 0;

      button {
        @include pseudowh($width: calc(29px + (35 - 29) * ((100vw - 320px) / (1920 - 320))),
          $height: calc(29px + (35 - 29) * ((100vw - 320px) / (1920 - 320))));
        @include flex_common;
        border: none;
        padding: 0;
        background-color: $white;
        z-index: 0;
        border-radius: 3px !important;

        i {
          font-size: 13px;
          color: var(--theme-color);
          margin-top: 1px;
        }

        &:focus {
          box-shadow: none;
        }
      }

      input {
        height: auto;
        background-color: transparent;
        border: none;
        padding: 0;
        text-align: center;
        font-size: 14px;
        color: $content-color;
      }
    }
  }
}

.qty-box {
  margin-top: 10px;
  width: 100%;
  max-width: 250px;

  .input-group {
    background-color: $light-gray;
    border-radius: 7px;
    padding: 4px;
    text-align: center;
    z-index: 0;

    &.theme-bg-white {
      background-color: $white;
    }

    button {
      @include pseudowh($width: calc(26px + (35 - 26) * ((100vw - 320px) / (1920 - 320))),
        $height: calc(26px + (35 - 26) * ((100vw - 320px) / (1920 - 320))));
      @include flex_common;
      border: none;
      padding: 0;
      background-color: $white;
      z-index: 0;
      border-radius: 4px !important;

      i {
        font-size: 13px;
        color: var(--theme-color);
        margin-top: 1px;
      }

      &:focus {
        box-shadow: none;
      }
    }

    input {
      height: auto;
      background-color: transparent;
      border: none;
      padding: 0;
      text-align: center;
      font-size: 14px;
      color: $content-color;
    }
  }
}

.deal-timer {
  margin: 11px 0;

  &.product-deal-timer {
    width: 290px;
    margin-top: 24px;

    ul {
      padding: calc(8px + (14 - 8) * ((100vw - 320px) / (1920 - 320)));
      background-color: $light-gray;
      border-radius: 8px;
      border: none;

      li {
        +li {
          &::before {
            left: -25px;

            [dir="rtl"] & {
              left: unset;
              right: -25px;
            }
          }
        }

        .counter {
          text-align: center;

          >div {
            font-size: calc(18px + (20 - 18) * ((100vw - 320px) / (1920 - 320)));
          }
        }
      }
    }
  }

  .deal-title {
    font-weight: 600;
    margin-bottom: 7px;
    letter-spacing: 0.4px;
  }

  ul {
    @include flex_common($dis: flex, $align: center, $justify: space-between);
    padding: 8px 12px;
    border: 1px dashed var(--theme-color);
    text-align: center;

    li {
      position: relative;

      +li {
        &::before {
          @include pos($pos: absolute, $content: ":");
          @include center(vertical);
          left: -20px;

          [dir="rtl"] & {
            left: unset;
            right: -20px;
          }
        }
      }

      .counter {
        >div {
          display: flex;
          align-items: center;

          h5 {
            font-weight: 600;
            margin-bottom: 4px;
          }

          h6 {
            color: $content-color;
            font-size: 13px;
          }
        }
      }
    }
  }
}

.category-menu {
  background-color: $light-gray;
  padding: calc(12px + (33 - 12) * ((100vw - 320px) / (1920 - 320)));
  border-radius: 5px;

  &-2 {
    background: linear-gradient(136.8deg, #f6f5f2 17.01%, #fdf7f0 93.97%);
  }

  h3 {
    margin-bottom: 40px;
    font-weight: 600;
    position: relative;
    display: inline-block;

    &::before {
      @include pos;
      @include pseudowh($width: 70%, $height: 2px);
      bottom: -8px;
      left: 0;
      background-color: var(--theme-color);

      [dir="rtl"] & {
        left: unset;
        right: 0;
      }
    }
  }

  ul {
    @include flex_wrap($dis: flex, $wrap: wrap, $gap: 23px);
    border-bottom: 1px dashed rgba($content-color, 0.5);

    &.value-list {
      margin-top: 35px;
      border: none;
    }

    li {
      display: block;
      width: 100%;

      &.pb-30 {
        margin-bottom: 30px;
      }

      .category-list {
        @include flex_wrap($dis: flex,
          $wrap: nowrap,
          $gap: calc(10px + (16 - 10) * ((100vw - 320px) / (1920 - 320))));
        align-items: center;
        color: $title-color;

        &:hover {
          h5 {
            a {
              letter-spacing: 0.5px;

              &:before {
                width: 70px;
              }
            }
          }
        }

        svg {
          @include pseudowh($width: 25px, $height: 25px);
          stroke: $title-color;
        }

        img {
          @include pseudowh($width: 25px, $height: 25px);
          object-fit: contain;
        }

        h5 {
          a {
            color: $title-color;
            -webkit-line-clamp: 1;
            -webkit-box-orient: vertical;
            display: -webkit-box;
            overflow: hidden;
            position: relative;
            z-index: 0;
            transition: all 0.3s ease-in-out;

            &:hover {
              color: $title-color;
            }

            &::before {
              @include pos;
              @include pseudowh($width: 0, $height: 3px);
              bottom: 0;
              left: 0;
              opacity: 0.5;
              border-radius: 50px;
              background-color: var(--theme-color);
              z-index: -1;
              transition: all 0.3s ease-in-out;

              [dir="rtl"] & {
                left: unset;
                right: 0;
              }
            }
          }
        }

        i {
          margin-left: auto;
        }
      }
    }
  }

  &.menu-xl {
    position: sticky;
    top: 130px;

    ul {
      gap: 28px;
      border-bottom: none;

      li {
        .category-list {
          img {
            width: 35px;
            height: 35px;
          }

          h5 {
            font-size: 17px;
          }
        }
      }
    }
  }
}

.product-border {
  border: 1px solid rgba($content-color, 0.3);
  border-radius: 10px;
  padding: 0 14px;

  &.border-row {
    .slick-slider {
      .slick-list {
        margin: 0 -14px;
      }
    }

    .row {
      >div {
        &:last-child {
          border-top: 1px solid #ddd;
        }
      }
    }
  }

  .slider-5-1 {
    .slick-slide {
      >.row {
        border-right: 1px solid rgba($content-color, 0.3);
        margin-left: 0;

        [dir="rtl"] & {
          border-left: 1px solid rgba($content-color, 0.3);
          border-right: unset;
        }
      }
    }
  }

  >.product-box-slider {
    >div {
      >.row {
        >div {
          &:nth-child(n + 6) {
            border-top: 1px solid rgba($content-color, 0.3);
          }

          &:nth-child(5n + 5) {
            .product-box {
              border-right: none;

              [dir="rtl"] & {
                border-left: none;
                border-right: unset;
              }
            }
          }

          @media screen and (max-width: 616px) {
            &:nth-child(5n + 2) {
              .product-box {
                border-right: none;

                [dir="rtl"] & {
                  border-left: none;
                  border-right: unset;
                }
              }
            }
          }
        }
      }
    }
  }
}

.product-box {
  padding: calc(8px + (14 - 8) * ((100vw - 320px) / (1920 - 320)));
  border-right: 1px solid rgba($content-color, 0.3);
  position: relative;
  transition: all 0.3s ease-in-out;
  overflow: hidden;

  &:hover {
    .product-image {
      a {
        img {
          transform: scale(1.1);
        }
      }

      .product-option {
        bottom: 15px;
        opacity: 1;
      }
    }
  }

  &.product-box-bg {
    background-color: $light-gray;
    border-radius: 5px;
    padding: calc(10px + (14 - 10) * ((100vw - 320px) / (1920 - 320)));
    border: none;

    .qty-box {
      .input-group {
        background-color: $white;
        box-shadow: 0 0 8px rgba($title-color, 0.04);
      }
    }

    +.product-box-bg {
      margin-top: 20px !important;

      @include mq-max(sm) {
        margin-top: 10px !important;
      }
    }
  }

  &.product-white-bg {
    background-color: $white;
    border-radius: calc(9px + (16 - 9) * ((100vw - 320px) / (1920 - 320)));
    padding: calc(10px + (14 - 10) * ((100vw - 320px) / (1920 - 320)));
    border: 1px solid #eee;

    .qty-box {
      .input-group {
        background-color: $white;
        box-shadow: 0 0 8px rgba(34, 34, 34, 0.04);
      }
    }

    &:hover {
      box-shadow: 0 15px 70px rgba($black, 0.07);
    }
  }

  .label-tag {
    position: absolute;
    top: 24px;
    left: 0;
    padding: 5px 13px;
    background-color: #ffba00;
    font-size: 14px;
    color: $white;
    font-weight: 500;
    z-index: 1;

    &::after {
      @include pos;
      @include pseudowh($width: 15px, $height: 15px);
      top: 16px;
      left: 44px;
      border-bottom: solid 15px #faba02;
      border-left: solid 15px transparent;
      border-right: solid 15px transparent;
      transform: rotate(0);
      z-index: 1;
    }

    &::before {
      @include pos;
      @include pseudowh($width: 15px, $height: 15px);
      top: 0;
      left: 44px;
      border-bottom: solid 15px #faba02;
      border-left: solid 15px transparent;
      border-right: solid 15px transparent;
      transform: rotate(180deg);
      z-index: 1;
    }

    span {
      font-weight: 700;
    }
  }

  .product-image {
    text-align: center;
    padding: 20px;
    position: relative;

    @include mq-max(xs) {
      padding: 8px;
    }

    .product-option {
      @include center(horizontal);
      @include flex_common($dis: flex, $align: center, $justify: space-between);
      position: absolute;
      bottom: -5px;
      background-color: $white;
      width: 70%;
      padding: 10px 5px;
      border-radius: 7px;
      box-shadow: 0 0 8px rgba($title-color, 0.12);
      opacity: 0;
      transition: all 0.3s ease-in-out;
      max-width: 320px;

      @include mq-max(xs) {
        display: none;
      }

      li {
        position: relative;
        width: 33.33%;

        a {
          color: $title-color;

          .feather {
            @include pseudowh($width: 17px, $height: auto);
            color: $content-color;
          }
        }

        +li {
          &:after {
            @include pos;
            @include pseudowh($width: 1px, $height: 100%);
            top: 0;
            left: 0;
            background-color: $content-color;
            opacity: 0.3;

            [dir="rtl"] & {
              left: unset;
              right: 0;
            }
          }
        }
      }
    }

    img {
      @include pseudowh($width: 100%, $height: 140px);
      object-fit: contain;
      transition: all 0.3s ease-in-out;
    }
  }

  .product-detail {
    a {
      color: $title-color;
      display: block;

      &:hover {
        color: $title-color;
      }

      h6 {
        &.name {
          font-weight: 600;
          line-height: 22px;
          height: 45px;
          -webkit-line-clamp: 2;
          -webkit-box-orient: vertical;
          display: -webkit-box;
          overflow: hidden;

          &-2 {
            -webkit-line-clamp: 1;
          }
        }
      }
    }

    .product-rating {
      flex-wrap: wrap;
      gap: calc(4px + (7 - 4) * ((100vw - 320px) / (1920 - 320)));

      h6 {
        font-weight: 500;
      }
    }
  }

  .progress {
    height: 6px;
    border-radius: 0;
    margin-top: calc(14px + (22 - 14) * ((100vw - 320px) / (1920 - 320)));
  }

  .stock {
    font-size: 14px;
    color: var(--theme-color);
    margin-left: auto;
  }

  .sold {
    font-weight: 600;
    margin-top: 3px;
    line-height: 21px;
    font-size: calc(14px + (15 - 14) * ((100vw - 320px) / (1920 - 320)));

    .price {
      font-weight: 600;
      margin-top: 0;
    }

    del {
      font-weight: 400;
      margin-left: 6px;
      font-size: calc(13px + (14 - 13) * ((100vw - 320px) / (1920 - 320)));
    }

    &.weight {
      font-size: 13px;
      margin: calc(5px + (8 - 5) * ((100vw - 320px) / (1920 - 320))) 0;
      line-height: normal;
    }
  }

  .qty-box {
    max-width: 210px;
  }

  .counter-box {
    display: block;
    position: relative;

    h6 {
      margin-top: 0;
    }

    .addtocart_btn {

      .add-button,
      .add_cart {
        @include flex_common;
        position: absolute;
        bottom: 0;
        right: 0;
        padding: 7px 11px;
        background-color: var(--theme-color);
        color: $white;
        font-size: 16px;
        font-weight: 600;
        transition: all 0.3s ease;

        span {
          position: relative;
          left: unset;
          right: unset;
          bottom: unset;
          font-size: 15px;
          margin-right: 8px;

          @include mq-max(xs) {
            display: none;
          }
        }
      }

      .qty-box {
        display: none;
        transition: all 0.3s ease;
        z-index: 1;
        margin-top: 0;
        max-width: 155px;

        &.open {
          display: block;
          position: absolute;
          right: 0;
          bottom: 0;
          width: 100%;
          transition: all 0.3s ease;
        }
      }

      .input-group {
        box-shadow: 0 2px 4px 0 #dedede;
        border-radius: 2px;
        padding: 4px;
        text-align: center;
        z-index: 0;
        background-color: $white;

        .btn {
          padding: 7px 12px;
          font-size: 10px;
          line-height: 1;
          border-radius: 2px !important;
          background-color: $light-gray;

          i {
            font-size: 15px;
          }
        }

        .form-control {
          padding: 2px;
          text-align: center;
          border-color: #f9f9f9;
        }
      }
    }
  }

  .price {
    margin-top: 16px;
    font-weight: 600;
  }
}

.product-list {
  border: 1px solid rgba($content-color, 0.3);
  border-radius: 5px;
  padding: calc(18px + (30 - 18) * ((100vw - 320px) / (1920 - 320)));

  li {
    display: flex;
    align-items: center;
    height: 80px;
    position: relative;

    +li {
      margin-top: 50px;

      @include mq-max(sm) {
        margin-top: 44px;
      }

      @include mq-max(2xs) {
        margin-top: 30px;
      }

      &:after {
        @include pos;
        top: -28px;
        right: 0;
        border-bottom: 1px dashed rgba($content-color, 0.3);
        width: calc(100% - 103px);

        [dir="rtl"] & {
          right: unset;
          left: 0;
        }
      }
    }

    .offer-product {
      @include pseudowh;
      display: flex;
      align-items: center;

      &:hover {
        .offer-image {
          transform: scale(1.1);
        }

        .offer-detail {
          a {
            .name {
              color: var(--theme-color);
            }
          }
        }
      }

      .offer-image {
        width: 80px;

        @include mq-max(4xl) {
          width: 90px;
        }

        img {
          @include pseudowh($width: 80px, $height: 80px);
          object-fit: contain;
          transition: all 0.3s ease-in-out;

          @include mq-max(4xl) {
            @include pseudowh($width: 90px, $height: 70px);
          }

          @include mq-max(sm) {
            @include pseudowh($width: 70px, $height: 70px);
          }
        }
      }

      .offer-detail {
        @include pseudowh($width: calc(100% - 80px), $height: 80px);
        padding-left: calc(12px + (23 - 12) * ((100vw - 320px) / (1920 - 320)));
        position: relative;
        display: flex;
        align-items: center;

        [dir="rtl"] & {
          padding-left: unset;
          padding-right: calc(12px + (23 - 12) * ((100vw - 320px) / (1920 - 320)));
        }

        a {
          display: block;
          color: $title-color;

          &:hover {
            color: $title-color;
          }

          .name {
            -webkit-line-clamp: 1;
            -webkit-box-orient: vertical;
            display: -webkit-box;
            overflow: hidden;
            font-weight: 600;
            margin-top: -1px;
            transition: all 0.3s ease-in-out;
          }
        }

        span {
          font-size: 14px;
          margin-top: 5px;
          color: $content-color;
          font-weight: 400;
        }

        .price {
          margin-top: 6px;
          font-weight: 600;
          margin-bottom: -2px;
        }
      }
    }
  }
}

.product-category {
  padding: calc(19px + (36 - 19) * ((100vw - 320px) / (1920 - 320))) calc(16px + (25 - 16) * ((100vw - 320px) / (1920 - 320)));
  background-color: $light-gray;

  .product-category-list {
    column-count: 2;

    [dir="rtl"] & {
      padding-left: unset;
      padding-right: 0;
    }

    li {
      display: block;
      padding-left: 22px;
      font-size: 14px;
      color: $content-color;
      position: relative;
      width: 140px;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;

      [dir="rtl"] & {
        padding-left: unset;
        padding-right: 22px;
      }

      &::before {
        @include pos;
        @include pseudowh($width: 7px, $height: 7px);
        top: 50%;
        transform: rotate(45deg) translateY(-50%);
        left: 0;
        border: 1px solid $content-color;

        [dir="rtl"] & {
          left: unset;
          right: 8px;
        }
      }

      +li {
        margin-top: calc(14px + (26 - 14) * ((100vw - 320px) / (1920 - 320)));
      }

      &:last-child {
        a {
          display: flex;
          align-items: center;

          &:hover {
            color: var(--theme-color);
          }

          i {
            margin-left: 8px;
          }
        }

        &::before {
          display: none;
        }
      }
    }
  }
}

.product-slider {
  padding-bottom: 5px;

  &:hover {
    .product-slider-image {
      img {
        transform: scale(1.1) rotate(4deg);
      }
    }
  }

  .product-slider-image {
    border-radius: 5px;
    overflow: hidden;
    display: block;

    img {
      transition: all 0.3s ease-in-out;
    }
  }

  .product-slider-detail {
    backdrop-filter: blur(4px);
    width: 90%;
    margin: -50px auto 0;
