      font-weight: 500;
      transition: all 0.3s ease-in-out;
      font-size: calc(15px + (16 - 15) * ((100vw - 320px) / (1920 - 320)));
      white-space: nowrap;
      line-height: 1;
      padding: 11px 19px;
      margin: 0;
      border-radius: 6px;
      display: flex;
      align-items: center;
      gap: 5px;

      .feather {
        width: 18px;
        height: 18px;
      }
    }
  }

  .theme-box {
    background-color: $white;
  }
}